.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.cell {
    width: 8vmin;
    height: 8vmin;
    border: 1px solid black;
}

input {
    font-size: 190%;
    text-align: center;
}

body {
    margin: 20px;
}

#submitButton {
    margin-top: 20px;
    height: 30px;
    width: 75px;
    font-size: 20px;
}